*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }
  
  :root{
    --mainColor: #fbfbfb;
    --mainColorLight: #3BB271;
    --secondaryColor: #3BB271;
    --textColor: black; 
  }
  
  header  {
    display: flex;
    align-items: center;
    justify-content: left;
    height : 80px;
    padding: 10px;
    background-color: var(--mainColor);
    color: var(--textColor);
  }
  
  nav a{
    margin: 0.4rem;
    color: var(--textColor);
    text-decoration: none;
    font-size: 25px;
    font-weight: bolder;
  }
  
  nav a:hover{
    color: var(--secondaryColor);
  }
  
  header .nav-btn{
    padding: 5px;
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: var(--textColor);
    visibility: hidden;
    opacity: 0;
    font-size: 1.8rem;
    
  }

  .cat-Select {
    height: 30px;
    border-radius: 4px 0px 0px 4px;
  }

  input{
    width:220px;
    height: 30px;
    border: none;
    border-radius: 0px 4px 4px 0px;
    font-size: 20px;
    padding-left: 50px;
    background-color: white;
    color: grey;
    border: solid 1px;
    border-color: grey;
  }

  header button{
    width:80px;
    height: 32px;
    font-size: 16px;
    background-color: #3BB271;
    color: #fbfbfb;
    border-color: black;
    border: solid;
    border-width: 2px;
  }

  header button:hover{
    width:80px;
    height: 32px;
    font-size: 16px;
    background-color: #2a8051;
    color: #fbfbfb;
    border-color: black;
    border: solid 2px;
  }

  .nav-space-filler {
    width: 4vw;
  }
/* 
  .page-links {
    display: flex;
    justify-content: flex-end;
  } */

  .cc{
    font-size: 10px;
    right: 0;
    padding-top: 29px;
    padding-right: 10px;
    position:absolute;
  }

  
  
  @media only screen and (max-width: 1300px){
    header .nav-btn{
      visibility: visible;
      opacity: 1;
    }
  
    header nav{
      position: fixed;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 1.5rem;
      background-color: var(--mainColor);
      transition: 1s;
      transform: translateY(-100vh);
    }
  
    header .responsive_nav{
      transform: none;
    }
    
    nav .nav-close-btn{
        position: absolute;
        top: 2rem;
        right: 2rem;
    }

    nav a{
        font-size: 1.5rem;
    }


    
  }